<template>
  <div class="login">
        <hr>
        <fb-signin-button
        :params="fbSignInParams"
        @success="onSignInSuccess"
        @error="onSignInError">
        Sign in with Facebook
      </fb-signin-button>
      
      <button v-show="false" @click="printProfileDetails">My profile</button>
  </div>
</template>

<script>
import VueNotifications from 'vue-notifications'
    /* eslint-disable */
    import Vue from 'vue'
    import FBSignInButton from 'vue-facebook-signin-button'
    Vue.use(FBSignInButton)
    export default {
        name: 'HelloWorld',
        notifications: {
            showLoginFailNotification: {
                type: VueNotifications.types.error,
                title: 'Some wrong happens',
                message: 'That\'s the fail, your login seems to work wrong...'
            },
             showLoginSuccessNotification: {
                type: VueNotifications.types.success,
                title: 'You did it!',
                message: 'That\'s the success, your login seems to work propertly...'
            }
        },
        data() {
            return {
                fbSignInParams: {
                    scope: 'email,user_likes',
                    return_scopes: true
                }
            }
        },
        methods: {
            printProfileDetails(){
              console.log('Profile details:')  
              this.$store.dispatch('updateMyProfileDetails')
            },
            onSignInSuccess(response) {
                let vm = this
                console.log('RES', response)
                FB.api('/me', dude => {
                    console.log(`Good to see you, ${dude.name}.`, dude)
                })
        
                console.log('Dispatch', response.authResponse.accessToken)
                this.$store.dispatch('authenticateWithFacebook', { accessToken: response.authResponse.accessToken })
                .then(() => {
                    vm.showLoginSuccessNotification()
                })
                .catch(err => {
                    vm.showLoginFailNotification()
                })

            },
            onSignInError(error) {
                console.log('OH NOES', error)
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .fb-signin-button {
        /* This is where you control how the button looks. Be creative! */
        display: inline-block;
        padding: 4px 8px;
        border-radius: 3px;
        background-color: #4267b2;
        color: #fff;
    }
</style>



