<template>
  <div class="hello">
    <h1>Futsal calendar in Paris</h1>
    <p>
      Everyone can sign in using Facebook and create/join a Futsal event.
      <br>
      A Futsal event has a max. number of players, an address, a datetime, and organizers contact details.
      <br>
      There is a global comment/chat section to discuss and share prior and after matches.
      <br>
      Questions, Ideas, help? Write to the creator address: arancibiajav@gmail.com
    </p>
    <ul v-show="false">
      <router-link to="/login">Login</router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Paris Futsal'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



