<template>
  <div id="app">
    <div class="grid-8-center nav grid-noGutter">
      <div class="col-1_md-12 logo-wrapper">
        <img class="logo" src="./assets/logo.png">
      </div>
      <div class="col-4_md-12 logo-wrapper">
        <h3 class="logo-text">Paris Futsal</h3>
      </div>
      <div class="col_md-12"></div>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  created: function () {
    this.$store.dispatch('refreshToken')
  }
}
</script>

<style lang="scss">
body{
  margin:0px;
  padding:0px;
}
.nav{
  padding-top:10px;
  padding-bottom:10px;
  
}
.logo-text{
      position: relative;
    top: -6px;
  text-align:left;
  @media only screen and (max-width : 64em) {
    top: 0px;
    text-align:center;
  }
}
.logo{
  max-width:50px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



